  a {
    position: relative;
    text-decoration: none;
  }
  
  a:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 5px;
    bottom: -20px;
    left: 0;
    background-color: #07137C;
    visibility: hidden;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transition: all 0.3s ease-in-out 0s;
    transition: all 0.3s ease-in-out 0s;
  }
  
  .black.active a:before{
    visibility: visible;
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
  
  button {
    border: 0;
    cursor: pointer;
    height: 20px;
    position: relative;
    width: 50px;
    -webkit-appearance: none;
    -moz-appearance: none;
box-shadow: 0px 2px 12px rgba(0, 3, 61, 0.0557528);
border-radius: 19px;
margin: .3rem 0 0 1.5rem;

  }

  button:focus,
  button:active { 
    outline: none;
   }
  button::-moz-focus-inner { 
    border: 0; 
  }
  
  button.on {
    content: "ON";
  }

  button.off{
    content: "OFF";
  }
  
  button.on .pin {
    left: 55%;
  }
  
