
.header{
    display: flex;
}

.imgy{
    width: 100% !important;
}

.bg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    -webkit-box-shadow: 0px 2px 12px rgba(0, 3, 61, 0.0557528);
    -moz-box-shadow: 0px 2px 12px rgba(0, 3, 61, 0.0557528);
    box-shadow: 0px 2px 12px rgba(0, 3, 61, 0.0557528);
}

.readMore{
    display: contents;
}

.readMore p{
    margin-top: .6rem;
}

.img{
 margin-left: 10px !important;
 margin-top: 9px;
}

.miniHeading{
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 22px;
}

.Lastimg {
    margin-left: -35px !important;
    margin-top: 5px;
    height: 100%;
}

.cleaners{
    border-radius: 15px;
}

.testimonyCards{
    width: 100%;
    height: auto;
    left: 122px;
    top: 1652px;
    box-shadow: 0px 2px 12px rgba(0, 3, 61, 0.0557528);
    -webkit-box-shadow:  0px 2px 12px rgba(0, 3, 61, 0.0557528);
    -moz-box-shadow:  0px 2px 12px rgba(0, 3, 61, 0.0557528);
    border-radius: 4px;
    padding: 1.5rem 0 0 0; 
}

.fullHouse{
    width: 100%;
}

.testimonyCards h5{
    font-family: Helvetica;
font-style: normal;
font-weight: bold;
font-size: 19.8px;
line-height: 23px;
}

.testimonyCards p{
    font-family: Helvetica;
    font-style: normal;
font-weight: normal;
font-size: 12px;
line-height: 16px;
letter-spacing: 0.373333px;

}


.accordion-wrapper {
    width: 100%;
    margin: 0 auto;
    box-shadow: 0px 2px 11px rgba(0, 3, 61, 0.0557528);
    border-radius: 4px;
  }

  .iconAcc{
    background: url('../images/Path.png');
    background-repeat: no-repeat;
    width: 20px;
    height: 20px;
  }

  .imgStyle{
      height: 100%;
      margin-top: .4rem;
  }

.moreLess{
    cursor: pointer;
    font-weight: bold;
    font-size: 10.08px;
}
  
@media (min-width:300px) and (max-width: 800px){
    .testimonyCards{
        width: 100%;
        height: auto !important;
        left: 122px;
        top: 1652px;
        box-shadow: 0px 5px 10px rgba(0, 10, 79, 0.0745465);
        border-radius: 4px;
    }
}

